.section {
  position: relative;

  padding: 100px 160px;

  background-color: #f2f2f2;

  &:first-child {
    background-color: #f2f2f2;
  }

  &:nth-child(even) {
    background-color: #eee;
  }

  &__heading {
    position: absolute;
    left: 0;
    right: 0;
    top: -0.5em;

    margin: auto;

  }

  &__gallery,
  &__consultation,
  &__info,
  &__testemonials,
  &__contacts,
  &__contact {
    margin: 0 auto;
  }
}

@media screen and (max-width: 1100px) {
  .section {
    padding: 40px;
  }
}

@media (max-width: 767px) {
  .section {
    padding: 40px 10px;
  }
}
